.dispatch-container {
  background-color: $background-dispatch;
  border-radius: 5px;
  position: relative;
}

.select-dispatch {
  width: $select-width;
  height: $select-height;
  position: relative;
  z-index: $z-index-xxs;
  border-radius: $border-select;
}

.location-image {
  width: $location-width;
  height: $location-height;
}

.down {
  position: absolute;
  bottom: 26px;
  left: 40px;
  z-index: $z-index-xxl;
}